import React, { useEffect, useState } from "react";
import styled from "styled-components";
// ICONS
import Translate from "../../shared/Translate";
import Image from "../../shared/Image";
import {
  Facebook,
  Instagramm,
  Linkedin,
  twitter,
} from "../../../assets/images";
import Date from "../../shared/Date";
import { i18n } from "src/i18n";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "react-share";
import moment from "moment";
import { StarOutlined, StarFilled } from "@ant-design/icons";
import AuthService from "src/modules/auth/authService";
import authSelectors from "src/modules/auth/authSelectors";
import { Tooltip } from "antd";
import authActions from "src/modules/auth/authActions";
import { useDispatch, useSelector } from "react-redux";

export default function Header({ entity, user, userIsLoading }) {
  const dispatch = useDispatch();

  const currentUser = useSelector(authSelectors.selectCurrentUser);
  const [isFav, setIsFav] = useState(false);

  useEffect(() => {
    if (currentUser && currentUser.favorites) {
      currentUser.favorites.mediatique?.map((media) => {
        if (media.id === entity?.id) {
          setIsFav(true);
        }
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentUser, entity]);

  const addToFavourite = async (id) => {
    currentUser.favorites.mediatique.push(id);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    dispatch(authActions.doRefreshCurrentUser());
    setIsFav(true);
  };
  const popFromFavourite = async (id) => {
    var index = 0;
    currentUser.favorites.mediatique.map((media, mediaIndex) => {
      if (media.id === id) {
        index = mediaIndex;
        return;
      }
    });
    currentUser.favorites.mediatique.splice(index, 1);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    dispatch(authActions.doRefreshCurrentUser());
    setIsFav(false);
  };

  return (
    <HeaderLayout>
      <span className="categoryBadge">
        {i18n(`entities.mediatique.enumerators.type.${entity["type"]}`)}
      </span>
      <h1 className="h1">{Translate.Trans("title", entity)}</h1>

      <div
        className="detail__author"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <div className="author__left">
          <div className="left__author__date">
            {moment(entity?.updatedAt).format("DD-MM-YYYY")}
          </div>
        </div>
        <div className="detaillEvenement__socialMedia">
          <div className="socialMedia__title">{i18n("common.Partager")} :</div>
          <div className="detaillEvenement__socialMedia">
            <div>
              <FacebookShareButton url={window.location.href}>
                <FacebookIcon size={35} round />
              </FacebookShareButton>
            </div>

            <div>
              <TwitterShareButton url={window.location.href}>
                <TwitterIcon size={35} round />
              </TwitterShareButton>
            </div>
            <div>
              <LinkedinShareButton url={window.location.href}>
                <LinkedinIcon size={35} round />
              </LinkedinShareButton>
            </div>
            {currentUser !== null ? (
              <div>
                {isFav ? (
                  <Tooltip title={i18n("common.unfavoris")}>
                    <StarFilled
                      style={{ color: "#e1011a", fontSize: 35 }}
                      onClick={() => popFromFavourite(entity.id)}
                    />
                  </Tooltip>
                ) : (
                  <Tooltip title={i18n("common.favoris")}>
                    <StarOutlined
                      style={{ color: "#e1011a", fontSize: 35 }}
                      onClick={() => addToFavourite(entity.id)}
                    />
                  </Tooltip>
                )}
              </div>
            ) : null}
          </div>
        </div>
      </div>
    </HeaderLayout>
  );
}

const HeaderLayout = styled.section`
  .categoryBadge {
    background: #f8d7da;
    border-radius: 3px;
    color: var(--dark--red);
    padding: 0.4rem 0.5rem;
    font-size: 12px;
    font-family: "Proxima Nova";
  }
  .author__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    color: #4f4f4f;
  }

  h1 {
    font-family: "Bebas Neue Pro";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 43px;
    text-transform: uppercase;
    color: var(--violet);
    margin: 1rem 0;
  }

  @media (max-width: 1171px) {
    h1 {
      font-size: clamp(25px, 5vw, 40px);
      line-height: 1.2;
    }
  }
  .detaillEvenement__socialMedia {
    display: flex;
    padding: 0;
    align-items: center;
    column-gap: 24px;
  }
  @media (max-width: 767px) {
    width: 100%;
    padding: 0 0.5rem;
  }
`;
